<!-- 顺序练题 -->
<template>
	<view>
		<!-- 倒计时 -->
		<view class="countdown" v-if="(type==='模拟考试')?true:false">
			<view>
				<image :src="cdn+'/icon/countdown.svg'" mode="aspectFill"></image>
				<text>倒计时：{{fix2(countdownTime.m)+':'+fix2(countdownTime.s)}}</text>
			</view>
		</view>

		<!-- 导航栏 -->
		<view class="nav" v-else>
			<view class="left"
				:style="{'color':nav.left.isAct?'#fff':'#333','background':nav.left.isAct?'#29c563':'#ffffff'}"
				@click="nav.left.isAct=true;nav.right.isAct=false">
				答题模式
			</view>
			<view class="right"
				:style="{'color':nav.right.isAct?'#fff':'#333','background':nav.right.isAct?'#29c563':'#ffffff'}"
				@click="nav.right.isAct=true;nav.left.isAct=false">
				背题模式
			</view>
		</view>
		<!-- 主体 -->
		<view class="body">
			<view class="question w">
				<text>{{question.titleInformation}}</text>
				<view class="tixing">
					<image :src="cdn+'/image/question/questionType.png'" mode="aspectFill"></image>
					<text>{{(question.form==1)?'多选':'单选'}}</text>
				</view>
			</view>
			<image :src="cdn+'/subject'+courseCode+'/'+question.picture" mode="aspectFit" class="pic w"
				v-show="question.picture==''?false:true" style="background-color: #e0e3dd;"></image>
			<image :src="cdn+'/subject'+courseCode+'/'+question.scpicture" mode="aspectFit" class="pic w"
				v-show="((question.scpicture=='')||!(question.scpicture))?false:true" style="background-color: #e0e3dd;"></image>
			<view class="answer w">
				<view class="son" v-for="(k,i) in question.answers" :class="" @click="clickAnswer(i)"
					v-if="k.option!=''">
					<image :src="cdn+'/icon/study/exam/'+ (isCorrect?'correct.svg':'err.svg')" mode="aspectFill"
						v-if="false"></image>
					<view v-else :style="{'backgroundColor':option[i].isAct?'#29c563':'',}"></view>
					<text>{{k.name +'、' +k.option}}</text>
				</view>
			</view>
			<view class="correct w">
				<text v-show="true">我的答案： <text style="color: #333333;">{{myChoose}}</text> </text>
				<text v-show="isAnswer">正确答案： <text style="color: #333333;">{{question.answer}}</text> </text>
				<view class=""></view>
				<button class="subBtn" @click="sub()" v-if="!isAnswer">提交答案</button>
				<button class="nextBtn" @click="getNext()" v-else>下一题</button>
			</view>

			<!-- 			<view class="explain w">
				<view class="title">
					<view></view>
					<text>官方解释</text>
				</view>
				<text class="content">{{questionMsg.explain}}</text>
			</view> -->

		</view>
		<!-- 题目列表 -->
		<view class="tiList" v-show="isShowList">
			<view class="mask" @click="isShowList=false"></view>
			<view class="content">
				<view class="son" v-for="(k,i) in 100">
					{{fix2(i+1)}}
				</view>
			</view>
		</view>
		<!-- 成绩 -->
		<view class="score" v-show="isSub">
			<!-- 用v-show进度条会消失... -->
			<view class="mask"></view>
			<view class="content">
				<view class="title">
					<view></view>
					<text>考试成绩</text>
					<view></view>
				</view>
				<view class="progressBar">
					<qiun-data-charts type="arcbar" :opts="opts" :chartData="chartData" :reshow="isSub" />
					<view class="text">
						<text>剩余未做</text>
						<text>4题</text>
					</view>
				</view>
				<text class="isPass">考试合格</text>
				<view class="fenshu">
					当前考试成绩：<text>96分</text>
				</view>
				<view class="btns">
					<button class="continue" @click="isSub=false">继续考试</button>
					<button class="sub" @click="$win.show('score/score')">现在交卷</button>
				</view>
			</view>
		</view>

		<!-- 底部 -->
		<view class="bottom">
			<!-- <view class="son" style="width: 100rpx;">
				<image :src="cdn+'/icon/study/exam/correctNum.svg'" mode="aspectFill"></image>
				<text>100</text>
			</view>
			<view class="son" style="width: 100rpx;margin-left: 50rpx;">
				<image :src="cdn+'/icon/study/exam/errNum.svg'" mode="aspectFill"></image>
				<text>100</text>
			</view> -->
			<view class="son" style="width: 180rpx;margin-left: 50rpx;" @click="isShowList=true">
				<image :src="cdn+'/icon/study/exam/questionList.svg'" mode="aspectFill"></image>
				<text>{{id}}/{{sum}}</text>
			</view>
			<view class="son" @click="isShoucang=!isShoucang" style="width: 150rpx;margin-left: 100rpx;"
				v-if="(type==='顺序练题')?true:false">
				<image :src="cdn+'/icon/study/exam'+(isShoucang?'/collected.svg':'/collect.svg')" mode="aspectFill">
				</image>
				<text :style="{'color':isShoucang?'#FC6624':''}">{{isShoucang?'已收藏':'收藏'}}</text>
			</view>
			<button v-else class="jiaojuan" @click="isSub=true">交卷</button>

		</view>
	</view>
</template>


<script>
	import {
		mapActions,
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				cdn: this.$conf.CDN_HOST,
				type: '',
				isShowList: false,
				courseCode: null,
				// id:1,
				//记录用户选项
				option: [{
					isAct: false,
				}, {
					isAct: false,
				}, {
					isAct: false,
				}, {
					isAct: false,
				}], //选项false未选，true已选
				//倒计时
				countdownTime: {
					m: 45,
					s: 0,
				},

				answer: [{
					option: 'A',
					content: ''
				}, {
					option: 'B',
					content: ''
				}],
				myChoose: '',
				//是否已经回答
				isAnswer: false,
				//是否正确
				isCorrect: false,
				//是否收藏
				isShoucang: false,
				//是否交卷
				isSub: false,
				// 进度条
				chartData: {
					series: [{
						name: "完成度",
						color: "#2fc25b",
						data: 0.96
					}]
				},
				opts: {
					extra: {
						arcbar: {
							type: "circle",
							width: null,
							lineCap: "round"
						}
					}
				},
				nav: {
					left: {
						text: '答题模式',
						isAct: true
					},
					right: {
						text: '背题模式',
						isAct: false
					}
				},
				questionMsg: {
					pic: '',
					type: '单选',
					question: '选未取得驾驶证的学员在道路上学习驾驶技能,下列哪种做法是正确',
					answer: [{
							isCorrect: false, //是否答对
							text: '使用所学车型的教练车单独驾驶学习',
						},
						{
							isCorrect: false, //是否答对
							text: '使由所学车型的教练车由教练员指导由',
						},
						{
							isCorrect: false, //是否答对
							text: '使用私家车由教练员随车指导',
						},
						{
							isCorrect: false, //是否答对
							text: '右前方路口视野受阻如有突然冲出车辆容易引发事故',
						},
					],
					correct: '右前方路口视野受阻如有突然冲出车辆容易引发事故', //正确答案
					explain: '右前方路口视野受阻如有突然冲出车辆容易引发事故',
				}
			}
		},
		computed: {
			...mapState({
				question: state => state.study.questionMsg,
				id: state => state.study.id,
				sum: state => state.study.sum
				// option:state=>state.study.option,
			})
		},
		methods: {
			...mapActions({
				getQuetion_fun: 'study/getQuetion',
				getPro_fun: 'study/getQuetionId',
				savePro_fun: 'study/savePro',
				saveMis_fun: 'study/saveMistake',
			}),
			...mapMutations({
				idAdd_fun: 'study/idAdd',
			}),
			//显示两位数
			fix2(n) {
				if (n < 10)
					n = '0' + n
				return n
			},
			toPX() {
				//根据用户设备屏幕大小设置宽度
				this.px = uni.getWindowInfo().screenWidth / 750
				console.log('lwlsb', this.px)
				//给进度条宽度赋值
				this.opts.extra.arcbar.width = 13 * this.px
			},
			//考试倒计时
			countdown() {
				var t = setInterval(() => {
					if (this.countdownTime.s == 0) {
						this.countdownTime.s = 59
						this.countdownTime.m--
					} else {
						this.countdownTime.s--
					}
					if (this.countdownTime.m == 0 && this.countdownTime.s == 0) {
						clearInterval(t)
						return 0
					}
				}, 1000)
			},
			//判断正确答案
			answerJudge(item) {
				if (item != this.questionMsg.correct) {

				}
			},
			//返回答案是否正确的图标
			backAnswerIcon(n) {
				if (n == 1) {
					return '/correct.svg'
				} else if (n == 0) {
					return '/err.svg'
				} else {
					return ''
				}
			},
			//我的答案
			myAnswer() {
				let str = ''
				for (let i = 0; i < this.option.length; i++) {
					if (this.option[i].isAct) {
						str += String.fromCharCode(65 + i)
					}
				}
				this.myChoose = str
				return str
			},
			//点击答案
			clickAnswer(n) {
				if(this.question.form==1){
					console.log('多选');
					if (this.isAnswer) return
					this.option[n].isAct=!this.option[n].isAct
				}
				else{
					console.log('单选');
					if (this.isAnswer) return
					for (let i = 0; i < this.option.length; i++) {
						this.option[i].isAct = false
					}
					this.option[n].isAct = true
				}
				
				this.myAnswer()
			},
			//提交答案
			sub() {
				let n = 0
				for (let i = 0; i < this.option.length; i++) {
					if (this.option[i].isAct) {
						continue
					} else {
						n++
						console.log('lnn')
					}
					if (n == 4) {
						this.$win.nlog('请选择答案')
						return
					}
				}
				this.isAnswer = true
				let answer = this.myAnswer()
				if (answer == this.question.answer) {
					console.log('答案正确');
					this.isCorrect = true
				} else {
					console.log('答案错误');
					this.isCorrect = false
					this.saveMis(this.id)
				}
				this.savePro()
			},
			//下一题
			getNext() {
				console.log('id1', this.id);
				this.idAdd_fun()
				console.log('id2', this.id);
				this.isAnswer = false
				this.myChoose = ''
				this.getQuetion(this.courseCode)
				//清空答案选项
				for (let i = 0; i < this.option.length; i++) {
					this.option[i].isAct = false
				}
			},
			//获取题目（逐题获取）
			async getQuetion(courseCode) {
				let data = {
					id: this.id+1,
					courseCode,
				}
				let res = await this.getQuetion_fun(data)
			},
			//获取做题进度
			async getPro(courseCode) {
				let data = {
					userId: this.$db.getSync('userId'),
					courseCode,
				}
				let res = await this.getPro_fun(data)
			},
			//保存进度
			async savePro() {
				let data = {
					userId: this.$db.getSync('userId'),
					id: this.id,
					courseCode: this.courseCode
				}
				let res = await this.savePro_fun(data)
			},
			//保存错题
			async saveMis(id) {
				let data = {
					userId: this.$db.getSync('userId'),
					courseCode:this.courseCode,
					id,
				}
				let res = await this.saveMis_fun(data)
			}
		},
		onLoad(option) {
			this.courseCode = option.courseCode
			console.log('练题页拿到课程码', this.courseCode);
			this.type = option.type
			if (this.type === '模拟考试') {
				this.countdown()
				this.toPX()
			}
		},
		onShow() {
			console.log('show', this.courseCode);
			this.getPro(this.courseCode).then((res) => {
				// console.log(res);
				this.getQuetion(this.courseCode)
			})

		}
	}
</script>

<style lang="scss">
