		border-radius: 16rpx;
		z-index: 99;

		.mask {
			position: absolute;
			top: -20vh;
			left: -55rpx;
			width: 100vw;
			height: 100vh;
			opacity: 0.5;
			background: #000000;
		}

		.content {
			display: flex;
			flex-direction: column;
			align-items: center;
			position: absolute;
			top: 0;
			left: 0;
			width: 640rpx;
			height: 698rpx;
			background: #ffffff;
			border-radius: 16rpx;

			.title {
				display: flex;
				flex-direction: row;
				align-items: center;
